import {actions, action} from '../actions'

export function selectAd(ad) {
  return dispatch => {
    dispatch(action(actions.selectAd, ad))
  }
}

export function updateMenuItems(adItems, activeItem) {
  return dispatch => {
    const newAdItems = adItems.map((item, i) => ({
      ...item,
      active: activeItem === (i + 1),
    }))
    dispatch(action(actions.updateMenuItems, newAdItems))
  }
}
